import { memo } from 'react'
import css from './Social.module.scss'
import classnames from 'classnames'
import SocialItem from 'components/atoms/SocialItem/SocialItem'
import IconInstagram from 'assets/icons/IconInstagram'
import IconFacebook from 'assets/icons/IconFacebook'
import IconTwitter from 'assets/icons/IconTwitter'
import { PropsWithClassName } from 'specs/index.spec'

function Social ({
  className,
  isDark
}: {
  isDark?: boolean
} & PropsWithClassName) {
  const data = {
    instagram: `https://instagram.com`,
    facebook: `https://facebook.com`,
    twitter: `https://twitter.com`
  }

  return (
    <ul className={classnames(css.list, className)}>
      <li className={css.item}>
        <SocialItem
          url={data.instagram}
          Icon={IconInstagram}
          label="Follow us in Instagram"
          isDark={isDark}
        />
      </li>
      <li className={css.item}>
        <SocialItem
          url={data.facebook}
          Icon={IconFacebook}
          label="Follow us in Facebook"
          isDark={isDark}
        />
      </li>
      <li className={css.item}>
        <SocialItem
          url={data.twitter}
          Icon={IconTwitter}
          label="Follow us in Twitter"
          isDark={isDark}
        />
      </li>
    </ul>
  )
}

export default memo(Social)